/* p-benefits
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
.p-benefits-system
==============================================================*/
.p-benefits-system {
    .c-table {
        @include responsive(md) {
            margin-top: rem(78);
        }
    }
}

/*==============================================================
.p-benefits-userVoice
==============================================================*/
.p-benefits-userVoice {
    &__wrapper {
        padding: rem(24) 0;

        @include responsive(md) {
            display: flex;
            justify-content: space-between;
            padding: 0;
            margin-top: rem(23);
        }
    }

    &__content {
        @include responsive(md) {
            width: 48%;
        }
    }

    &__content:last-of-type {
        margin-top: rem(26);

        @include responsive(md) {
            margin-top: 0;
        }
    }

    &__day {
        color: $color-accent;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
        }
    }

    &__name {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(20);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(20 / 20);
        margin-top: rem(4);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
            margin-top: rem(13);
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(7);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(29 / 16);
            margin-top: rem(25);
        }
    }

    &__img {
        @include responsive(md) {
            margin-top: rem(38);
        }
    }
}
